@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500&display=swap");

@tailwind base;
@tailwind components;
@tailwind utilities;

/*setting the color red of the navbar heading pages ling when they are active */
.active{
    color:red;
}

body{
    background-color:#f3e8d9;
}
.popupBox {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    top: 0%;
    left: 0%;
    background: rgba(0, 0, 0, 0.696);
  }
  
  .closebtn {
    position: absolute;
    right: 5%;
    top: 14%;
    margin-top: 1px;
    font-size: 28px;
    cursor: pointer;
  }